* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* background: radial-gradient(rgba(43,55,96,1),rgba(11,16,35,1)) */
}

body {
  background: radial-gradient(rgba(43,55,96,1),rgba(11,16,35,1));
  height: 100vh;
}

.App {
  text-align: center;
  color: white;
}

.App_title {
  margin: 30px;
}

.resultsinSearch {
  display: grid;
  grid-template-columns: 33% 33% 33%;

}

.Search_form {
  text-align: center;
}

.Search_form input[type="text"]{
  padding: 15px;
  border-radius: 24px;
  border: 2px solid #fff;
  outline: none;
  font-size: large;
  width: 250px;
  height: 40px;
  transition: 0.2s;
}

.Search_form input[type="text"]::before {
  font-family: "Font Awesome 5 Free"; font-weight: 900; content: "\f007";
}

.Search_form input[type="text"]:focus {
  border: 2px solid #fff;
  border-radius: 24px;
  box-shadow: 0 1px 10px 0 rgba(255, 255, 255, 0.2), 0 6px 20px 0 rgba(255, 255, 255, 0.19);
}

.Search_form button {
  margin-top: 1px;
  font-size: medium;
  font-weight: 600;
  padding: 5px;
  border-radius: 10px;
  background-color: #84e296;
  color: black;
  border: 2px solid #84e296;
  cursor: pointer;
}

.Search_form button:hover {
  background-color: white;
  color: #84e296;
  border: 2px solid white;
}

.Result {
  margin: 40px;
  cursor: pointer;
}

@media only screen and (max-width : 320px) {
  .resultsinSearch {
    display:flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .Result img{
    margin-top: 20px;
    height: 300px;
    width: 200px;
  }

  .popup {
    overflow: auto;
    display: flex;
    justify-content: center ;
  }

  .popup_close {
    font-size: small;
    border: none;
    
  }

  .popup header h1{
    margin-top: 100px;
    font-size: x-large;
  }

  .popup_image img{
    position: relative;
    left:40px; 
    margin-left: 0;
    height: 400px;
    width: 300px;
  }

  .popup_content h2 {
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
  }
}

.Result_title {
  height: 125px;
  width: 100%;
}

.Result_image {
  box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.2), 0 6px 20px 0 rgba(255, 255, 255, 0.19);
  transition: 0.3s;
}

.Result_image:hover {
  transform: scale(1.1);
  
}

.popup {
  height: 100%;
  width: 100%;
  background-color: #59cd90;
  position: fixed;
  top: 0;
  left: 0;
  color: #38405f;
}

.popup_close {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}

.popup header {
  margin-top: 40px;
  font-size: x-large;
  
}

.popup_image img {
  margin-top: 70px;
  box-shadow: 0 4px 8px 0 #0e131f;
  position: static;
  left: 100px;
  top: 100px;
}

.popup_content {
  float: right;
  width: 700px;
  position: relative;
  right: 250px;
  top: 100px;
}

.popup_content_cast {
  margin-top: 30px;
}

.popup_content_cast h3{
  color: #ff3366;
}

.popup_content_rating {
  margin-top: 30px;
  display: grid;
  /* grid-template-columns: ; */
}

.popup_content_rating h3 {
  color: #ff3366;
}

